<template>
  <div class="listview-top">
    <img class="bg" :src="playlist.coverImgUrl" alt="" />
    <div class="listtopnav">
      <div class="back" @click="$router.go(-1)">
        <svg class="icon fanhui" aria-hidden="true">
          <use xlink:href="#icon-sdf"></use>
        </svg>
        <div class="title">歌单</div>
      </div>
      <div class="right">
        <svg class="icon search" aria-hidden="true">
          <use xlink:href="#icon-sousuo"></use>
        </svg>

        <svg class="icon lb" aria-hidden="true">
          <use xlink:href="#icon-liebiao1"></use>
        </svg>
      </div>
    </div>
    <div class="content">
      <div class="conleft">
        <img :src="playlist.coverImgUrl" alt="" />
        <div class="concount">{{ changeValue(playlist.playCount) }}</div>
      </div>
      <div class="conright">
        <h3>{{ playlist.name }}</h3>
        <div class="author">
          <img
            :src="playlist.creator.avatarUrl"
            alt=""
          />
          <div class="nickname">{{playlist.creator.nickname}}</div>
        </div>
        <div class="description">
          {{ playlist.description }}
        </div>
      </div>
    </div>
    <div class="iconList">
      <div class="iconitem">
        <svg class="icon " aria-hidden="true">
          <use xlink:href="#icon-pinglun"></use>
        </svg>
        <span>{{ playlist.commentCount }}</span>
      </div>
      <div class="iconitem">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-fenxiang"></use>
        </svg>
        <span>{{ playlist.shareCount }}</span>
      </div>
      <div class="iconitem">
        <svg class="icon " aria-hidden="true">
          <use xlink:href="#icon-download"></use>
        </svg>
        <span>下载</span>
      </div>
      <div class="iconitem">
        <svg class="icon " aria-hidden="true">
          <use xlink:href="#icon-duoxuan"></use>
        </svg>
        <span>多选</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["playlist"],
  mounted() {
  },
  methods: {
    changeValue(num) {
      let res = 0;
      if (num >= 100000000) {
        res = num / 100000000;
        res = res.toFixed(2) + "亿";
      } else if (num > 10000) {
        res = num / 10000;
        res = res.toFixed(2) + "万";
      }
      return res;
    },
  },
};
</script>

<style lang="less">
.listview-top {
  width: 7.5rem;
  padding: 0 0.4rem;
  .bg {
    position: fixed;
    left: 0;
    top: 0;
    width: 7.5rem;
    height: auto;
    z-index: -1;
    filter: blur(35px);
  }
  .listtopnav {
    margin-top: 0.3rem;
    display: flex;
    justify-content: space-between;
    height: 0.8rem;
    align-items: center;
    font-size: 0.4rem;
    .back .title {
      margin-top: 0.05rem;
      font-size: 0.35rem;
    }
    .icon {
      width: 0.6rem;
      height: 0.6rem;
      fill: #fff;
    }
    .icon.search {
      width: 1rem;
      height: 1rem;
      color: #fff;
    }
    .icon.lb {
      margin-top: 0.15rem;
    }
    .back,
    .right {
      display: flex;
      color: #fff;
    }
    .icon.fanhui {
      width: 0.5rem;
      margin-right: 0.3rem;
    }
  }
  .content {
    padding: 0.5rem 0;
    display: flex;
    justify-content: space-between;
    .conleft {
      position: relative;
      img {
        width: 2.5rem;
        height: 2.5rem;
        border-radius: 0.1rem;
      }
      .concount {
        position: absolute;
        top: 0;
        right: 0.1rem;
      }
    }
  }
  .conright {
    width: 3.5rem;
    color: #fff;
    .author {
      display: flex;
      margin: 0.2rem 0;
      align-items: center;
      img {
        width: 0.6rem;
        height: 0.6rem;
        border-radius: 0.3rem;
      }
      .nickname {
        color: #ccc;
        font-size: 0.26rem;
        margin-left: 0.1rem;
      }
    }
    .description {
      font-size: 0.24rem;
      color: #ccc;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      line-height: #fff;
    }
  }
  .iconList{
    display: flex;
    justify-content: space-around;
    width: 7.1rem;
    .iconitem{
      display: flex;
      flex-direction: column;
      align-content: center;
      .icon{
        width: 0.6rem;
        height: 0.6rem;
        fill: #fff;
      }
      span{
        font-size: 0.3rem;
        padding-top: 0.3rem;
        color: #fff;
      }
    }
  }
}
</style>